<template>
  <div id="product">
    <div class="product__container">
      <div class="thisProduct">
        <div class="thisProduct__img">
          <div class="thisProduct__carousel">
            <splide :options="{rewind : true}">
              <splide-slide
              v-for="(item, index) in Object.values(this_product.images).flat(2)"
              :key="index">
                <img :src="item">
              </splide-slide>
            </splide>
          </div>
          <div class="thisProduct__lightbox">
            <div class="lightbox__main">
              <img :src="lightbox_main" alt="" class="img">
              <img src="../../assets/icon/new_tag.png" alt="" class="is_new">
            </div>
            <div class="lightbox__list">
              <img class="image"
              v-for="(item, index) in Object.values(this_product.images).flat(2)"
              :src="item" :key="index"
              @click="lightbox_index = index"
              @mouseover="lightbox_main = item">
              <vue-gallery-slideshow
              :images="Object.values(this_product.images).flat(2)"
              :index="lightbox_index"
              @close="lightbox_index = null"></vue-gallery-slideshow>
            </div>
          </div>
        </div>
        <div class="thisProduct__main">
          <p class="thisProduct__title">{{this_product.title}}
            <span class="thisProduct__id">{{this_product.id}}</span>
          </p>
          <div class="thisProduct__content" v-html="this_product.content"></div>
          <hr class="thisProduct__hr">
          <p class="thisProduct__price">NT${{to_numberStr(this_product.price)}}</p>
          <div class="thisProduct__quantity">
            <b-form-spinbutton
            v-model="ordering_product.quantity" min="1" max="100"></b-form-spinbutton>
          </div>
          <div class="thisProduct__function">
            <div class="functionBtns">
              <a href="#" class="secondary"
              @click.prevent="add_to_cart">加入購物車</a>
              <a href="#" class="primary"
              @click="checkout_directly">直接購買</a>
            </div>
            <p class="remark">台灣本島地區全站商品免運配送</p>
          </div>
          <div class="thisProduct__favorite">
            <label>
              <input type="checkbox" v-model="is_favorite"
              @click="toggle_user_favorite">
              <span class="true" v-if="is_favorite">
                <img src="../../assets/icon/gold_heart_icon.png" alt="">
                已加入喜好項目
              </span>
              <span class="false" v-if="!is_favorite">
                <img src="../../assets/icon/gold_heart_empty_icon.png" alt="">
                加入喜好項目
              </span>
            </label>
          </div>
          <div class="thisProduct__description">
            <b-tabs content-class="mt-3">
              <b-tab :title="item.title"
              v-for="(item, index) in this_product.description"
              :key="index"
              v-html="item.text">
              </b-tab>
            </b-tabs>
          </div>
        </div>
      </div>
      <div class="relatedProduct">
        <div class="relatedProduct__header">
          <hr class="relatedProduct__header__hr">
          <span class="relatedProduct__header__span">
            相關商品
          </span>
        </div>
        <ul class="relatedProduct__list">
          <li v-for="(item, index) in related_products"
          :key="index">
            <a href="#"
            @click="go_page(`front/online_store/${$route.params.category}/${item.id}`)">
              <img :src="item.images.main" alt="">
              <p class="title">{{item.title}}</p>
              <p class="id">{{item.id}}</p>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { Splide, SplideSlide } from '@splidejs/vue-splide';
import VueGallerySlideshow from 'vue-gallery-slideshow';

export default {
  watch: {
    $route() {
      this.get_this_product();
    },
  },
  props: ['products', 'cart', 'user'],
  components: {
    Splide,
    SplideSlide,
    VueGallerySlideshow,
  },
  data() {
    return {
      this_product: {},
      related_products: [],
      ordering_product: {
        img: '',
        title: '',
        id: '',
        price: 0,
        quantity: 1,
      },
      lightbox_main: '',
      lightbox_index: null,
      is_favorite: false,
    };
  },
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    get_this_product() {
      this.this_product = this.products
        .find((item) => item.id === this.$route.params.id);

      if (this.$route.params.category === 'all') {
        this.related_products = JSON.parse(JSON.stringify(this.products));
      } else {
        this.related_products = this.products
          .filter((item) => JSON.stringify(item.category)
            .includes(this.$route.params.category));
      }
      this.related_products.splice(
        this.related_products.findIndex((item) => item.id === this.this_product.id),
        1,
      );
      if (this.related_products.length > 6) {
        this.related_products.length = 6;
      }

      this.ordering_product.img = this.this_product.images.main;
      this.ordering_product.title = this.this_product.title;
      this.ordering_product.id = this.this_product.id;
      this.ordering_product.price = this.this_product.price;

      [this.lightbox_main] = Object.values(this.this_product.images).flat(2);

      this.is_favorite = this.user.favorite.includes(this.this_product.id);
    },
    to_numberStr(num) {
      const numStr = num.toString().split('.')[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',').toString();
      return numStr;
    },
    add_to_cart() {
      this.push_order();
      this.cart.sum = 0;
      this.cart.products.forEach((item) => {
        this.cart.sum += item.price * item.quantity;
      });
      alert('加入購物車成功');
      this.$bus.$emit('rerender_cart');
    },
    checkout_directly() {
      this.push_order();
      this.cart.sum = 0;
      this.cart.products.forEach((item) => {
        this.cart.sum += item.price * item.quantity;
      });
      this.$nextTick(() => {
        if (this.cart.products.length === 0) {
          $('#front').removeClass('cart--hasProduct');
        } else {
          $('#front').addClass('cart--hasProduct');
        }
      });
      this.go_page('front/checkout/checkout1');
    },
    push_order() {
      const isObj = this.cart.products
        .find((item) => item.id === this.$route.params.id);
      if (typeof (isObj) === 'object') {
        // 如果購物車已經有該商品
        isObj.quantity += this.ordering_product.quantity;
      } else {
        this.cart.products.push(JSON.parse(JSON.stringify(this.ordering_product)));
      }
      this.ordering_product.quantity = 1;
    },
    toggle_user_favorite() {
      // if false means it changes to true
      if (this.is_favorite) {
        this.user.favorite.splice(this.user.favorite
          .findIndex((item) => item === this.this_product.id), 1);
      } else {
        this.user.favorite.push(this.this_product.id);
      }
    },
  },
  created() {
    this.get_this_product();
  },
};
</script>
